.loader {
    /*свг и/или анимация*/

    border: 9px solid #f3f3f3; /* Light grey */
    border-top: 8px solid blueviolet; /* Blue */
    border-radius: 50%;
    width: 90px;
    height: 90px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}